
import React, { Component, PropTypes } from 'react';
import { connect } from 'react-redux';
import Helmet from 'react-helmet';


export default class Login extends Component{
	constructor(props){
		super(props);
		this.state={
			email:'',
			password:'',
			showForgetPassword:false

		}
	}

	handleLoginInput(e){
		var name=e.target.name,value=e.target.value;
		this.setState({[name]:value});
	}

	login(e){
		e.preventDefault();
		var logindata1=this.state;
		console.log(logindata1)
		
		fetch('http://192.168.20.168:4000/api/login',{
			mode:'cors',
			method:"POST",
			headers: { 'Accept':'application/json , text/plain, */*',
						'Content-type':'application/json' },
			body:JSON.stringify(logindata1)
		}).then((res) => res.json())
		.then(res => {
			if(typeof res === "object"){
				console.log("response", res);
			}
		})
		.catch((err) => console.log(err))
	}

	forgetPassword(){
		this.setState({showForgetPassword:true})
		console.log("forget clicked")
		


			
	}
	render(){
		let s
		if(showForgetPassword){
			 s= <div>remember me!</div>
		}
		
		return(
				
					<div className="login_section">
						<div className="login_img">
							<span className="login_text">Log In</span>
							<img src="../assets/images/login/login.png" alt="login_img"/>
						</div>
						<div className="login_input">
							<form onSubmit={this.login.bind(this)}>
								<div className="form-group">
									<input type="email" name="email" className="user_inputs_login form-control" placeholder="Email" value={this.state.email} onChange={this.handleLoginInput.bind(this)}/>
								</div>
								<div className="form-group">
									<input type="password" name="password" className="user_inputs_login form-control" placeholder="Password" value={this.state.password} onChange={this.handleLoginInput.bind(this)}/>
								</div>
								<div className="form-group">
									<span><a onClick={this.forgetPassword.bind(this)}>Forgot password?</a></span>
								</div>
								
								<div className="form-group text-right">
									<input type="submit" name="Login" value="Log In" className="submit_button"/>
								</div>
							</form>
						</div>
						{s}
					</div>			
						
				
			
				
		)
	}
}